<template>
  <div class="clearfix" style="padding:5%; background: #eee8d2;">
    <div class="clearfix">
  		<ul class="clearfix fl ww">
        <li><h1>将进酒</h1></li>
  			<li>君不见，黄河之水天上来，</li>
  			<li>奔流到海不复回。</li>
  			<li>君不见，高堂明镜悲白发，</li>
  			<li>朝如青丝暮成雪。</li>
  			<li>人生得意须尽欢，莫使金樽空对月。</li>
  			<li>天生我材必有用，千金散尽还复来。</li>
  			<li>烹羊宰牛且为乐，会须一饮三百杯。</li>
  			<li>岑夫子，丹丘生，将进酒，杯莫停。</li>
  			<li>与君歌一曲，请君为我倾耳听。</li>
  			<li>钟鼓馔玉不足贵，但愿长醉不复醒。</li>
        <li>古来圣贤皆寂寞，惟有饮者留其名。 </li>
        <li>陈王昔时宴平乐，斗酒十千恣欢谑。</li>
        <li>主人何为言少钱，径须沽取对君酌。</li>
        <li>五花马，千金裘，</li>
        <li>呼儿将出换美酒，与尔同销万古愁。</li>
  		</ul>
    </div>
	</div>
</template>

<script>
export default {
  data () {
    return {
      title: '将进酒',
      msg: ''
    };
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

@font-face{
    font-family: 'yegenyouziti';
    src: url('./yegenyouziti.eot');
    src:
        url('./yegenyouziti.eot?#font-spider') format('embedded-opentype'),
        url('./yegenyouziti.woff') format('woff'),
        url('./yegenyouziti.ttf') format('truetype'),
        url('./yegenyouziti.svg') format('svg');
    font-weight: normal;
    font-style: normal;
}

ul li{
  font-family: 'yegenyouziti';
  line-height:2.5rem;
  float:right;
  list-style:none;
  width:2rem;
  padding:.5rem;
  border:1px solid #e71f19;

  height: 38rem;
  vertical-align: middle;
  padding-top: 1rem;
  margin-bottom: 1rem;
  font-size:2rem;}
ul li h1{font-size: 3rem; padding-top: 3rem; line-height:4rem }

</style>



