<template>
  <div class="clearfix" style="padding:5%; background: #eee8d2;">
    <div class="clearfix">
      <div class="ww mgb1">
        <input type="text" class="lh-l search" v-model="searchStr" @keyup="handlerSearchStr" placeholder="检索">
      </div>
      <div class="fl w5">
        <h1 class="al-c">唐诗</h1>
        <ul class="pd-5">
          <template v-for="item in list">
            <router-link :to="item.url">
              <li :class="item.s?'show':'hide'" v-if="item.type=='0'">{{item.name}}<span>{{item.time}}</span><span>{{item.author}}</span></li>
            </router-link>
          </template>
        </ul>
      </div>
      <div class="fl w5">
        <h1 class="al-c">宋词</h1>
        <ul class="pd-5">
          <template v-for="item in list">
            <router-link :to="item.url">
              <li :class="item.s?'show':'hide'" v-if="item.type=='1'">{{item.name}}<span>{{item.time}}</span><span>{{item.author}}</span></li>
            </router-link>
          </template>
        </ul>
      </div>
    </div>
	</div>
</template>

<script>
import scdata from './data';
export default {
  data () {
    return {
      list: [],
      searchStr: null,
      msg: ''
    };
  },
  mounted () {
    this.list = scdata;
  },
  methods: {
    handlerSearchStr () {
      let s = this.searchStr;
      if (!s.length || s.length === 0) {
        for (let i = 0; i < this.list.length; i++) {
          this.list[i].s = true;
        }
        return;
      }

      for (let i = 0; i < this.list.length; i++) {
        this.list[i].s = false;
        if (this.list[i].discript.indexOf(s) !== -1) {
          this.list[i].s = true;
        } else {
          this.list[i].s = false;
        }
      }
    },
    handlerItem (item) {
      this.$router.go('/jiang');
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

/*@font-face{
    font-family: 'yegenyouziti';
    src: url('./yegenyouziti.eot');
    src:
        url('./yegenyouziti.eot?#font-spider') format('embedded-opentype'),
        url('./yegenyouziti.woff') format('woff'),
        url('./yegenyouziti.ttf') format('truetype'),
        url('./yegenyouziti.svg') format('svg');
    font-weight: normal;
    font-style: normal;
}*/

ul li{
  font-family: 'yegenyouziti';
  line-height:2.5rem;
  float:right;
  list-style:none;
  width:2rem;
  padding:.5rem;
  border:1px solid #e71f19;

  height: 18rem;
  vertical-align: middle;
  padding-top: 1rem;
  margin-bottom: 1rem;
  font-size:2rem;}
ul li h1{font-size: 3rem; padding-top: 3rem; line-height:4rem }
ul li span{
  font-size: 1rem;
  display: inline-block;
  width: 1rem;
  line-height: 1.5rem;
  padding: .5rem;
  padding-top: .2rem;
}
.search{
  background-color: transparent;
  border: none;
  border-bottom: 1px solid #dad3b0;
  text-align: center;
  border-radius: 0;
}
input:focus, textarea:focus, keygen:focus, select:focus {
    outline-offset:0;
}
:focus {
    outline: -webkit-focus-ring-color auto 0px;
}
</style>



